import React, { Fragment, Component } from 'react';
import axios from 'axios';

class Card extends Component {

   constructor(props) {
       super(props)
   
       this.state = {
            posts:[]
       }
   }
   componentDidMount(){
     axios.get("https://api.spaceXdata.com/v3/launches?limit=100")
     .then(response => {
         this.setState({posts:response.data});
        console.log(response);
     })
     .catch(error => {
        console.log(error);
     })
   }
   

    render() {
        const {posts}=this.state
        return (
            <>
                {
                    posts.map(post => <Fragment key={post.id}>
                        <div className="card">
                     <div className="cardimg">
                     <img src={post.links.mission_patch} alt="logo" />
                     </div>
                     <div className="cardcont">
                         <h5 className="cardheader">{post.mission_name} #<span>{post.flight_number}</span></h5>
                         <p className="missionId"> Mission Ids:</p>
                             <ul className="missionIdUl">
                                <li className="missionIdUlli">{post.mission_id}</li>
                             </ul>
                         <p className="launchYear">Launch Year: <span className="bluetext">{post.launch_year}</span></p>
                         <p className="SuccessfulLaunch">Successful Launch: <span className="bluetext">{post.launch_success}</span></p>
                         <p className="SuccessfulLanding">Successful Landing: <span className="bluetext">{post.launch_year}</span></p>
                     </div>
                </div>
                    </Fragment>)
                }
            </>
        )
    }
}

export default Card;





