import React from 'react';
import Card from './Card';

const Home = () => {
    return(
        <>
            <div className="content">
        <h2>SpaceX Launch Programs</h2>
        <div className="flex-container">
            <div className="sidebar">
                <h4>Filter</h4>
                <p>launch Year</p>
                <div className="line"></div>
                <div className="yearbtn">
                    <ul>
                        <li><button type="button">2006</button></li>
                        <li><button type="button">2007</button></li>
                        <li><button type="button">2008</button></li>
                        <li><button type="button">2009</button></li>
                        <li><button type="button">2010</button></li>
                        <li><button type="button">2011</button></li>
                        <li><button type="button">2012</button></li>
                        <li><button type="button">2013</button></li>
                        <li><button type="button">2014</button></li>
                        <li><button type="button">2015</button></li>
                        <li><button type="button">2016</button></li>
                        <li><button type="button">2017</button></li>
                        <li><button type="button">2018</button></li>
                        <li><button type="button">2019</button></li>
                        <li><button type="button">2020</button></li>
                    </ul>
                </div>
                <div className="success">
                    <p>Successful Launch</p>
                    <div className="line"></div>
                <div className="yearbtn">
                    <ul className="wd">
                        <li><button type="button">True</button></li>
                        <li><button type="button">False</button></li>
                    </ul>
                </div>
                </div>
                <div className="landing">
                    <p>Successful Landing</p>
                    <div className="line"></div>
                <div className="yearbtn">
                    <ul className="wd">
                        <li><button type="button">True</button></li>
                        <li><button type="button">False</button></li>
                    </ul>
                </div>
                </div>
                
            </div>
            <div className="mainbar">
                <div className="cardContainer">
                    <Card />
                </div>
            </div>
        </div>
    </div>
        </>
    );
};

export default Home;